<template>
  <div :id="siteBanner" class="view intro hm-purple-light">
    <b-navbar toggleable="lg" type="dark" variant="info" class="bg-transparent">
      <b-navbar-brand href="#">
        <img id="nav-logo-img" src="@/assets/logo.jpg" alt="enginsoft-logo" />
        <span id="nav-logo-text">{{ siteTitle }}</span>
      </b-navbar-brand>

      <b-navbar-toggle target="nav-collapse"></b-navbar-toggle>

      <b-collapse id="nav-collapse" is-nav>
        <b-navbar-nav class="ml-auto">
          <b-nav-item v-if="this.$store.getters.homePage">
            <router-link to="/">Home</router-link>
          </b-nav-item>
          <span v-for="(page, index) in this.routes" :key="index">
            <b-nav-item v-if="page.acf.show_in_navbar">
              <router-link
                :to="page.post_title.toLowerCase().replace(/\s/g, '')"
              >
                {{ page.post_title }}
              </router-link>
            </b-nav-item>
          </span>
        </b-navbar-nav>
      </b-collapse>
    </b-navbar>
    <div class="container-fluid banner" id="top">
      <div class="container" id="text">
        <div class="col-md-5">
          <h1>{{ siteTitle }}</h1>
          <hr />
          <p>{{ siteDescription }}</p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import _ from "underscore";

export default {
  name: "navbar",
  data() {
    return {
      siteTitle: process.env.VUE_APP_TITLE,
      siteDescription: process.env.VUE_APP_DESCRIPTION,
    };
  },
  computed: {
    routes() {
      let r = _.sortBy(this.$store.state.pages, "menu_order");
      return r.filter(
        (x) => x.post_title.toLowerCase().replace(/\s/g, "") != "home"
      );
    },
    siteBanner() {
      if (process.env.VUE_APP_MODE == "TA") {
        return "ta_view";
      } else if (process.env.VUE_APP_MODE == "PW") {
        return "pw_view";
      } else if (process.env.VUE_APP_MODE == "SB") {
        return "sb_view";
      }
      return "sb_view";
    },
  },
};
</script>

