<template>
  <div class="container">
    <div class="content-styles">
      <div v-for="(block, index) in blocks" :key="index">        
        <component :value="block.value" :is="block.type"></component>
      </div>
    </div>
  </div>
</template>

<script>
import extract_gp_blocks from "./gutenbergparser.js";
import buttonblock from "./blocks/ButtonBlock.vue";
import formatblock from "./blocks/HtmlFormatBlock.vue";
import youtubeblock from "./blocks/YoutubeBlock.vue";
import imageblock from "./blocks/ImageBlock.vue";

export default {
  mixins: [extract_gp_blocks],
  components: { buttonblock, formatblock, youtubeblock, imageblock },
  props: {
    blocksRaw: String
  },
  computed: {
    blocks() {
      return this.extract_gp_blocks(this.blocksRaw);
    }
  }
};
</script>

