<template>
  <div class="d-flex flex-column img-box" :style="this.style">
    <div class="p-2">
      <img :src="source" :alt="alternate" :style="this.imgStyle" />
    </div>
    <em class="figureCaption" v-html="caption"></em>
  </div>
</template>

<script>
var DomParser = require("dom-parser");
var parser = new DomParser();

export default {
  props: {
    value: String
  },
  data() {
    return {
      source: "",
      alignment: "",
      style: "",
      imgStyle: "",
      caption: "",
      alternate: "",
      circular: "",
      height: "",
      width: "",
      imgSize: ""
    };
  },
  mounted() {
    var dom = parser.parseFromString(this.value);

    let block = dom.getElementsByClassName("wp-block-image")[0]
      ? dom.getElementsByClassName("wp-block-image")[0]
      : "";

    if (block.attributes) {
      block.attributes.forEach(a => {
        if (a.name === "class") {
          if (a.value.includes("is-style-circle-mask")) {
            this.circular = "clip-path: circle(" + 50 + "%" + " at center); ";
          }
          if (a.value.includes("size-thumbnail")) {
            this.imgSize = "thumbnail";
          }
          if (a.value.includes("size-medium")) {
            this.imgSize = "medium";
          }
          if (a.value.includes("size-large")) {
            this.imgSize = "large";
          }
          if (a.value.includes("size-full")) {
            this.imgSize = "full";
          }
        }
      });
    }

    this.caption = dom.getElementsByTagName("figcaption")[0]
      ? dom.getElementsByTagName("figcaption")[0].innerHTML
      : "";

    let imgTag = dom.getElementsByTagName("img")[0];

    if (imgTag.attributes) {
      imgTag.attributes.forEach(a => {
        if (a.name === "src") {
          this.source = a.value;
        }

        if (a.name === "height") {
          this.height = a.value + "px;";
        }

        if (a.name === "width") {
          this.width = a.value + "px;";
        }

        if (a.name === "alt") {
          this.alternate = a.value;
        }
      });
    }

    let figure = dom.getElementsByTagName("figure")[0]
      ? dom.getElementsByTagName("figure")[0]
      : "";

    if (figure.attributes) {
      figure.attributes.forEach(a => {
        if (a.name === "class") {
          if (a.value.includes("alignright")) {
            this.alignment = "float: right; margin-right: 5px;";
          }
          if (a.value.includes("alignleft")) {
            this.alignment = "float: left; margin-left: 5px;";
          }
          if (a.value.includes("aligncenter")) {
            this.alignment =
              "text-align: center; margin-left: auto; margin-right: auto; display: block;";
          }
        }
      });
    }

    if (this.height === "" || this.width === "") {
      switch (this.imgSize) {
        case "thumbnail":
          this.width = "40%;";
          this.height = "40%;";
          break;
        case "medium":
          this.width = "60%;";
          this.height = "60%;";
          break;
        case "large":
          this.width = "80%;";
          this.height = "80%;";
          break;
        case "full":
          this.width = "100%;";
          this.height = "100%;";
          break;
        default:
          this.width = "100%;";
          this.height = "100%;";
      }
    }

    this.style = this.alignment;

    this.imgStyle =
      "max-width:" + this.width + "max-height:" + this.height + this.circular;
  }
};
</script>

<style scoped>
img {
  width: 100%;
  height: 100%;
}

.img-box {
  padding: 5px;
}

.figureCaption {
  margin-top: 5px;
  text-align: center;
  margin-bottom: 20px;
}

.center-items {
  text-align: center;
  max-width: 560px;
}

.figureCaption {
  margin-top: 5px;
  text-align: center;
  margin-bottom: 20px;
}
</style>
